.tooltip {
  position: relative;
  display: inline-block;
}
.tooltip .tooltiptext {
  visibility: hidden;
  width: 150px;
  background-color: #fff;
  -webkit-box-shadow: 0px 3px 10px rgba(0,0,0,.25);
          box-shadow: 0px 3px 10px rgba(0,0,0,.25);
  color: #42526e;
  font-weight: 500;
  text-align: left;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  top: 15px;
  left: 80%;
  height: 100px;
}
.tooltip:hover .tooltiptext {
  visibility: visible;
}
.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top:  15%;
  right: 100%; /* To the left of the tooltip */
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}
